<template>
    <div class="scSearch">
      <div class="search">
        <form action="/">
          <van-search
            v-model="value"
            placeholder="搜索"
            show-action
            @search="onSearch"
          />
        </form>
      </div>
      <div class="searchContent">
        <div class="searchCon">
          <div class="lanList">
            <ul>
              <li v-for="item  in insuranceList" :key="item.id" @click="toDetail(item.id)">
                <img :src="item.imgs" alt="">
                <h3>
                  {{item.title}}
                </h3>
                <div class="sc_jflist">
                 <div class="person">已预约 <span>{{item.yu_num}}</span>人</div>
                </div>
              </li>
            </ul>
          </div>
      </div>
      </div>
      <a href="" class="kefu iconfont icon-kefu"></a>
    </div>
</template>

<script>
import img from "./../assets/img/timg.jpg"
import { apiGet } from '../common/js/api';
    export default {
        name: "sc_search",
      data(){
        const query = this.$route.query;
        return{
          value:"",
          query:query,
          insuranceList:[]
        }
      },
      created(){
        this.onSearch();
      },
      methods:{
        //触发搜索
        onSearch(){
          apiGet("/insurance/search",{
            key:this.value,
            cat_id:this.query.typeId
          }).then(res=>{
            this.insuranceList = res.data.data.list;
          })
        },
        //跳转保险详情
        toDetail(id){
          this.$router.push({
            path:'/xiangqin',
            query:{
              id
            }
          })
        }
      }
    }
</script>

<style lang="scss">
  .scSearch{
    width:100%;
    height: 100%;
    background: #E6E9ED;
    display: flex;
    flex-direction: column;
  }
  .van-search .van-cell{
    height: 0.42rem;
  }
  .van-cell__left-icon{
    line-height: 0.35rem;
  }
  .van-search__action{
    display: none;
  }
  .search{
    width: 100%;
    height:.76rem;
    background: #E6E9ED;
    .van-search{
      width: 100%;
      padding: 0.17rem 0.13rem;
      background: #E6E9ED !important;
      input{
        line-height: 0.4rem;
      }
    }
  }
  .person{
    color: #999999;
    font-size: 0.13rem;
    line-height: 0.28rem;
  }
  .searchContent{
    width: 100%;
    background: #E6E9ED;
    flex: 1;
    overflow: auto;
    .searchCon{
      position: relative;
      width: 3.5rem;
      overflow: hidden;
      margin: 0 auto;
     min-height: 0.6rem;
      .tab_list{
        position: absolute;
        top: 0;
        color: #9a9a9a;
        display: flex;
        justify-content: space-between;
        height:0.47rem;
        width: 100%;
        background: white;
        li{flex: 1;line-height: 0.47rem;text-align: center;font-size: 0.19rem;
          display: flex;
          justify-content: center;
          i{font-style: normal}
          div{
            width: 0.3rem;
            display: flex;
            flex-direction: column;
            line-height: 0.34rem;
            span{
              font-size: 0.2rem;
              display: block;
              width: 0.3rem;
              color: #bcbcbc;
              height: 0.1rem;
            }
            .icon-shangsanjiao{
              font-size: 0.16rem;
            }
            .blue{
              color: #3C7FEE;
            }
          }
        }
        .textblue{
          color: #3C7FEE;
        }
      }
      .lanList{
        width: 100%;
        background: #E6E9ED;
        display: flex;
        overflow: auto;
        flex-wrap: wrap;
        ul{
          border-radius: 0.04rem;
          width: 3.5rem;
          background: white;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          padding: 0.14rem 0.13rem;
        }
        li{
          width: 1.56rem;
          img{
            display: block;
            width: 1.56rem;
            height: 1.59rem;
          }
          h3{
            width: 1.56rem;
            font-size: 0.16rem;
            font-weight: 600;
            color: #333;
            line-height: 0.34rem;
          }
          .sc_jflist{
            height: 0.4rem;
            display: flex;
            justify-content: space-between;
            .jfnum{text-align: left;font-size: 0.16rem;color: red}
            .rsnum{text-align: right;font-size: 0.12rem;line-height: 0.24rem;color: #9a9a9a}
          }
        }
      }
    }
  }
  .mint-popup-right {
    width: 70%;
    height: 100%;
    background: white;
    .po_warp {
      position: relative;
      height: 100%;
      overflow: auto;
      .jifenlist{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        li{
          width: 1.04rem;
          margin: 0 0.13rem 0.17rem;
          height: 0.5rem;
          background: #E5E8EB;
          text-align: center;
          display: block;
          font-size: 0.17rem;
          line-height: 0.5rem;
          border-radius: 0.04rem;
        }
        .chiose{
          border: 1px solid #3C7FEE;
        }
      }
      .info {
        height: 0.31rem;
        background: #efefef;
        line-height: 0.31rem;
        text-indent: 0.13rem;
        font-size: 0.15rem;
        color: #9a9a9a;
      }
    }
    .money_area{
      padding-top: 0.21rem;
      overflow: hidden;
      padding-bottom: 0.2rem;
      span{float: left;margin: 0.15rem 0.06rem;width: 0.16rem;height: 1px;background: #bcbcbc}
      input{
        width: 40%;
        font-size: 0.13rem;
        color: #9a9a9a;
        line-height: 0.31rem;
        border: 1px solid #bcbcbc;
        border-radius: 0.04rem;
        height: 0.31rem;
        text-indent: 0.09rem;
        float: left;
        &:nth-of-type(1){margin-left: 5%}
      }

    }
    .bottom_type{
      position: fixed;
      bottom: 0;
      display: flex;
      width: 100%;
      font-size: 0.19rem;
      height: 0.47rem;
      button{
        flex: 1;
        text-align: center;
        line-height: 0.47rem;
      }
      button:nth-of-type(1){
        color: #3C7FEE;
        border: none;
        border-top: 1px solid #f5f5f5;
      }
      button:nth-of-type(2){
        color: white;
        background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      }
    }
  }
  .kefu{
    width: 0.52rem;
    height: 0.52rem;
    background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
    border-radius:50%;
    box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
    position: fixed;
    bottom: 1.5rem;
    display: block;
    text-align: center;
    line-height: 0.52rem;
    font-size: 0.28rem;
    color: white;
    z-index: 3;
    right: 0.11rem;
  }
</style>



